import React from "react";
import Link from "gatsby-link";
import { OutboundLink } from 'gatsby-plugin-google-analytics'

import ollie_glass from './images/people/ollie_glass.jpg'


export default () => (
  <div>

	<div className="grid">
	    <div className="col-8-12 mobile-col-8-12">
	       <div className="content">
				<h2>Data science, machine learning engineering &amp; data strategy</h2>

	            <p><a href="mailto:ollie@ollieglass.com">ollie@ollieglass.com</a> ~ <OutboundLink href="https://twitter.com/ollieglass">@ollieglass</OutboundLink> ~ <OutboundLink href="http://www.linkedin.com/in/ollieglass">LinkedIn</OutboundLink></p>
	       </div>
	    </div>

	    <div className="col-2-12 mobile-col-4-12 push-right">
	       <div className="content">
	           <img style={{ borderRadius: "50%" }} src={ollie_glass} alt="Ollie Glass" />
	       </div>
	    </div>
	</div>

	<p>I help companies understand and use machine learning.</p>

	<p>From discovering and evaluating ideas, designing and building data products and features, and helping teams transition to new ways of working with data, my work covers all aspects of data science management and delivery.</p>

	<p>Clients include venture-backed companies funded by Index Ventures, Balderton Capital, BGF and other top investors.</p>

	<p>If you're looking for a data scientist, if you have a project in mind or want to explore ideas, feel free to get in touch ollie@ollieglass.com</p>

	<p>Not sure where to start or how data science projects are run? Read my article on <Link to="/articles/managing-data-science-projects">managing data science projects</Link>.</p>

	<h2>About me</h2>

	<p>I discuss product development and data science with <OutboundLink href="https://twitter.com/probablygeorge">George Spencer</OutboundLink> (<OutboundLink href="https://www.rentify.com/">Rentify</OutboundLink>) and <OutboundLink href=
	"https://twitter.com/bengoldsmith90">Ben Goldsmith</OutboundLink> in the <OutboundLink href="http://www.balderton.com/">Balderton Capital</OutboundLink> podcast.</p>

	<p>"You don't need to come to a product oriented data scientist with a solution. You can come to them with an opportunity, or a problem, or a challenge in the business."</p>

	<iframe frameBorder="no" height="166" scrolling="no" src="https://w.soundcloud.com/player/?url=https%3A//api.soundcloud.com/tracks/232754996&amp;color=ff5500&amp;auto_play=false&amp;hide_related=false&amp;show_comments=true&amp;show_user=true&amp;show_reposts=false" width="100%"></iframe>



	<h2>Mailing list</h2>

	<p>I write an occasional newsletter about data science and machine learning in business, and interesting developments in technology. I also post when I'm giving talks and running workshops. <OutboundLink href='http://eepurl.com/dyFOzj'>You can sign up here</OutboundLink></p>

  </div>
);


